/* Standardized screen headers */
.screen-header {
  font-size: 1.5rem;
  font-weight: bold;
  line-height: 1.2;
  margin: 0;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

@media (min-width: 640px) {
  .screen-header {
    font-size: 1.75rem;
  }
}

@media (min-width: 768px) {
  .screen-header {
    font-size: 2rem;
  }
}

/* Theme-specific font families */
.theme-retro {
  font-family: 'Press Start 2P', 'Courier New', 'Monaco', 'Menlo', monospace !important;
}

.theme-arcade {
  font-family: 'Orbitron', 'Exo 2', 'Arial Black', sans-serif !important;
  font-weight: 700 !important;
  font-style: normal !important;
  letter-spacing: 0.5px !important;
}

.theme-alternative {
  font-family: 'Nunito', sans-serif !important;
}

/* Mobile-responsive font sizes */
.text-mobile-xs {
  font-size: 8px;
}

.text-mobile-sm {
  font-size: 10px;
}

.text-mobile-base {
  font-size: 12px;
}

.text-mobile-lg {
  font-size: 14px;
}

/* Responsive input styling */
@media (max-width: 640px) {
  .btn-retro {
    font-size: 8px;
    padding: 8px 12px;
    letter-spacing: 0.5px;
  }
  
  input[type="text"],
  input[type="number"],
  select,
  textarea {
    font-size: 10px !important;
  }
  
  .contestant-input {
    font-size: 8px;
  }
}